'use client'

import { Button, Label, Modal, TextInput, Dropdown } from "flowbite-react";
import { useEffect, useState } from "react";
import toast from "react-hot-toast";

import { useNewContact } from "@/services/contacts/newContact";

import { fetchContacts } from "@/services/contacts/fetchContacts";

export default function ModalContact()
{
    const [contacts, setContacts] = useState([]);

    useEffect(() => {
        fetchContacts().then((data) => {
            setContacts(data);
        });
    }, []);
   
    const [name, setName] = useState("");
    const [phone, setPhone] = useState("");

    return (
       <>
                     <Modal.Header>Adicionar movimentação</Modal.Header>
                        <Modal.Body>
                        <div className="space-y-6">
                            <div>
                                <div className="mb-2 block">
                                    <Label htmlFor="contato" value="Contato" />
                                </div>
                                <Dropdown label="Selecione o contato" size="sm">
                                    {contacts.map((contact) => (
                                            <Dropdown.Item key={contact.id}>{contact.name}</Dropdown.Item>
                                    ))}
                                </Dropdown>
                            </div>
                        </div>
                        </Modal.Body>
                        <Modal.Footer>
                        <Button color="dark" size="lg" onClick={() => {}
                        }>
                            Adicionar
                        </Button>
                        </Modal.Footer>
      </>
    )
}